
.hd-header-bar {
  text-align: center;
  line-height: 0.9rem;
  min-height: 0.9rem;
  color: #eee;
  background-color: #474a4f;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
}
.hd-header-bar a {
  font-size: 0.24rem;
  padding: 0 0.2rem;
  position: absolute;
  top: 0;
  right: 0;
}
.hd-header-bar-back {
  width: 0.9rem;
  position: absolute;
  top: 0;
  left: 0;
}
.hd-header-bar-title {
  font-size: 0.36rem;
  font-weight: normal;
}


