
.hd-alert {
  width: 70%;
  word-wrap: break-word;
  font-size: 0.28rem;
  color: #666;
  line-height: 1.5;
  text-align: center;
  border-radius: 0.06rem;
  background-color: #fafafa;
  box-shadow: 0 0.19rem 0.38rem rgba(0, 0, 0, .3),
                0 0.15rem 0.12rem rgba(0, 0, 0, .22);
}
.hd-alert-header {
  font-size: 0.32rem;
  font-weight: bold;
  color: #333;
  padding: 0.2rem 0.24rem 0;
  margin-bottom: -0.12rem;
}
.hd-alert-body {
  font-size: 0.28rem;
  padding: 0.36rem 0.24rem;
}
.hd-alert-footer {
  border-top: 0.01rem solid #ccc;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.hd-alert-button {
  font-size: 0.28rem;
  background-color: inherit;
  padding: 0.2rem 0;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
}
.hd-alert-button--cancel {
  color: #999;
  border-right: 0.01rem solid #ccc;
}
.hd-alert-button--confirm {
  color: #2db7f5;
}


