
.hd-dialog {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2000;
  background-color: transparent;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.hd-dialog.is-bottom {
  -webkit-box-align: end;
  -ms-flex-align: end;
  align-items: flex-end;
}
.hd-dialog.is-top {
  -webkit-box-align: start;
  -ms-flex-align: start;
  align-items: flex-start;
}
.hd-dialog.is-center {
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.hd-dialog-mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2001;
  background-color: rgba(0, 0, 0, 0.5);
}
.hd-dialog-body {
  width: 100%;
  z-index: 2002;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.hd-dialog-fade-enter {
  opacity: 1;
}
.hd-dialog-fade-enter-active {
  -webkit-transition: opacity .2s linear;
  transition: opacity .2s linear;
}
.hd-dialog-fade-leave-active {
  opacity: 1;
  -webkit-transition: opacity .2s linear;
  transition: opacity .2s linear;
}
.hd-dialog-fade-body-center-enter {
  -webkit-transform: scale(0);
  transform: scale(0);
}
.hd-dialog-fade-body-center-enter-active {
  -webkit-transition: -webkit-transform .2s linear;
  transition: -webkit-transform .2s linear;
  transition: transform .2s linear;
  transition: transform .2s linear, -webkit-transform .2s linear;
}
.hd-dialog-fade-body-center-leave-active {
  -webkit-transform: scale(0);
  transform: scale(0);
  -webkit-transition: -webkit-transform .2s linear;
  transition: -webkit-transform .2s linear;
  transition: transform .2s linear;
  transition: transform .2s linear, -webkit-transform .2s linear;
}
.hd-dialog-fade-body-bottom-enter {
  -webkit-transform: translateY(100%);
  transform: translateY(100%);
}
.hd-dialog-fade-body-bottom-enter-active {
  -webkit-transition: -webkit-transform .2s linear;
  transition: -webkit-transform .2s linear;
  transition: transform .2s linear;
  transition: transform .2s linear, -webkit-transform .2s linear;
}
.hd-dialog-fade-body-bottom-leave-active {
  -webkit-transform: translateY(100%);
  transform: translateY(100%);
  -webkit-transition: -webkit-transform .2s linear;
  transition: -webkit-transform .2s linear;
  transition: transform .2s linear;
  transition: transform .2s linear, -webkit-transform .2s linear;
}
.hd-dialog-fade-body-top-enter {
  -webkit-transform: translateY(-100%);
  transform: translateY(-100%);
}
.hd-dialog-fade-body-top-enter-active {
  -webkit-transition: -webkit-transform .2s linear;
  transition: -webkit-transform .2s linear;
  transition: transform .2s linear;
  transition: transform .2s linear, -webkit-transform .2s linear;
}
.hd-dialog-fade-body-top-leave-active {
  -webkit-transform: translateY(-100%);
  transform: translateY(-100%);
  -webkit-transition: -webkit-transform .2s linear;
  transition: -webkit-transform .2s linear;
  transition: transform .2s linear;
  transition: transform .2s linear, -webkit-transform .2s linear;
}
.hd-dialog-fade-body-left-enter {
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%);
}
.hd-dialog-fade-body-left-enter-active {
  -webkit-transition: -webkit-transform .2s linear;
  transition: -webkit-transform .2s linear;
  transition: transform .2s linear;
  transition: transform .2s linear, -webkit-transform .2s linear;
}
.hd-dialog-fade-body-left-leave-active {
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%);
  -webkit-transition: -webkit-transform .2s linear;
  transition: -webkit-transform .2s linear;
  transition: transform .2s linear;
  transition: transform .2s linear, -webkit-transform .2s linear;
}
.hd-dialog-fade-body-right-enter {
  -webkit-transform: translateX(100%);
  transform: translateX(100%);
}
.hd-dialog-fade-body-right-enter-active {
  -webkit-transition: -webkit-transform .2s linear;
  transition: -webkit-transform .2s linear;
  transition: transform .2s linear;
  transition: transform .2s linear, -webkit-transform .2s linear;
}
.hd-dialog-fade-body-right-leave-active {
  -webkit-transform: translateX(100%);
  transform: translateX(100%);
  -webkit-transition: -webkit-transform .2s linear;
  transition: -webkit-transform .2s linear;
  transition: transform .2s linear;
  transition: transform .2s linear, -webkit-transform .2s linear;
}


